@Plugin({
	options: {}
})
export default class Register {
  init () {
    const that = this;
    this.$form = this.$element.find('#register-form');

      this.$form.on('submit', function (e) {
        e.preventDefault();
		that.checkInputs();
	});

  }
	checkInputs() {
		const that = this;
		const username = that.$form.find("#username");
		const email = that.$form.find("#email");
		const phone =  that.$form.find("#phone");
		const password = that.$form.find("#password");
		const password2 = that.$form.find("#password2");

		if (username.val() === "") {
		  username.next('.error').removeClass('hidden');
		} else {
		  username.next('.error').addClass('hidden');
		}
		if (email.val() === "") {
			email.next('.error').removeClass('hidden');
		} else {
			email.next('.error').addClass('hidden');
		}
		if (password.val() === "") {
			password.next('.error').removeClass('hidden');
		} else {
			password.next('.error').addClass('hidden');
		}
		if (password2.val() === "") {
		  password2.next('.error').removeClass('hidden');
		} else {
		  password2.next('.error').addClass('hidden');
		}
		if (phone.val() === "") {
			phone.next('.error').removeClass('hidden');
		} else {
			phone.next('.error').addClass('hidden');
		}
	}
		
	isEmail(email) {
		return /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email);
	}
}