@Plugin
export default class isLogin {
  init () {
    this.checkIsLogin();
  }
  checkIsLogin() {
    const that = this;

    const userInfo = window.localStorage.getItem('userInfo');
    if (userInfo) {
      that.displayUserInfo(userInfo);
    } else {
      const token = window.localStorage.getItem('token');
      if (token) {
        that.getUserInfo(token);
      } else {
        const $loginArea = that.$element.find('.login-area');
        $loginArea.find('a').remove();
        const userElement = `
        <a href="login.html">
          ĐĂNG NHẬP
        </a>
        <a href="register.html">
          ĐĂNG KÝ
        </a>
        <a href="access.html">
        ĐĂNG TIN
        </a>
        `;
        $loginArea.append(userElement);
      }
    }
  }
  getUserInfo(token) {
    const that = this;
    $.ajax({
      method: that.options.method,
      url: that.options.getUserUrl,
      dataType: 'json',
      data: token,

      // beforeSend(){
      //   const loader = `<div class="loader"></div>`;
      //   that.$element.append(loader);
      // }
    })
      .done(function(res) {
        const data = JSON.stringify(res.data);
        window.localStorage.setItem('userInfo', data);
        const userInfo = window.localStorage.getItem('userInfo');
        that.displayUserInfo(userInfo);
      })
      .fail(function(e) {
        console.log(e);
      });
  }

  displayUserInfo (userInfo) {
    const that = this;
    const info = JSON.parse(userInfo);
    const $loginArea = that.$element.find('.login-area');
    $loginArea.find('a').remove();
    const userElement = `
    <a href="member.html">
      <img src="https://static123.com/uploads/images/2018/12/12/boy_1544603222.png" style="width: 50px; height: 50px;">
    </a>
    <a href="member.html" style = " margin-top: 13px;">
      ${info.userName}
    </a>
    <a href="access.html" style = " margin-top: 13px;">
    ĐĂNG TIN
    </a>
    `;
    $loginArea.append(userElement);
  }
}