import { data } from "jquery";

@Plugin
export default class Filter {
  init () {
      this.$district = this.$element.find('[data-district]');
      this.$ward = this.$element.find('[data-ward]');
      this.$form = this.$element.find('[data-form-filter]');
      this.getDistrict();
      this.getSearchResult();
  }

  getDistrict () {

    const that = this;
    this.$district.on('change', function() {
        const id = parseInt($(this).val());

        $.ajax({
            method: that.options.method,
            url: that.options.url,
            data: {"districtId": id},
            dataType: 'json',
          })
              .done(function(res) {
                const ward = res.filter(item => item.huyen_id === id && item);
                const wardElement = ward.map(item => {
                    return `<option value="${item.id}">${item.name}</option>`;
                });
                that.$ward.find('option').remove();
                that.$ward.append(wardElement);
                //sort
              })
              .fail(function(e) {
                console.log(e);
              });
    });

  }

  getSearchResult () {

    const that = this;

    this.$form.on('submit', function(event) {
        event.preventDefault();
        const data = $(this).serializeArray();

        $.ajax({
          method: that.options.method,
          url: that.options.searchUrl,
          data: data,
          dataType: 'json',
        })
          .done(function(res) {
            const { data } = res;
            const result = data.map( item => {
              return (
                `<div class="product">
                    <a href="detai.html">
                        <div class="img">
                            <img src=${item.image} alt=""/>
                        </div>
                    </a>
                    <a href="detai.html" class="title">${item.title}</a>
                    <div class="address">${item.address}</div>
                    <div class="area">${item.area}</div>
                    <div class="price">${item.price}</div>
                    <div class="date">${item.date}</div>
                    <a href="detai.html">Chi Tiết</a>
                </div>`
              )
            })
            that.$element.find('.loader').remove();
            $('.product-list .product').remove();
            $('.product-list').append(result);
          })
          .fail(function(e) {
            console.log(e);
          });
    });
  }
}