@Plugin({
    options: {}
  })
  export default class Information {
    init () {
      const that = this;
      that.fetchApi();
    }
    fetchApi () {
      const that = this;

      $.ajax({
        method: that.options.method,
        url: that.options.url,
        dataType: 'json',
        beforeSend(){
          const loader = `<div class="loader"></div>`;
          that.$element.append(loader);
        }
      })
          .done(function(res) {
            const { data } = res;
            const result = data.map( item => {
              return (
                `
                    <a class="clearfix" href="#"</a>
                    <div class="user_avatar">
                        <img src=${item.image} alt=""/>
                    </div>
                    <div class="user_meta">
                        <div class="inner">
                            <div class="user_name">${item.userName}</div>
                            <div class="user_verify" style="color: #555; font-size: 14px;">${item.phone}</div>
                        </div>
                    </div>
                    <ul></ul>
                    <li>
                        <span>Mã thành viên:</span>
                        <span style="font-weight: 700;"> ${item.userID}</span>
                    </li>
                    <a class="btn btn-danger btn-sm" href="access.html" style="font-size: 12px">Đăng tin</a>
                `
              );
            });
            that.$element.find('.loader').remove();
            that.$element.append(result);
          })
          .fail(function(e) {
            console.log(e);
          });
    }
  }
