@Plugin({
    options: {}
  })
  export default class Q1 {
    init () {
      const that = this;
      that.fetchApi();
    }
    fetchApi () {
      const that = this;
      $.ajax({
        method: that.options.method,
        url: that.options.url,
        dataType: 'json',
        beforeSend(){
          const loader = `<div class="loader"></div>`;
          that.$element.append(loader);
        }
      })
          .done(function(res) {
            const { data } = res;
            const result = data.map( item => {
              return (
                `<div class="product">
                            <a href="detai.html">
                                <div class="img">
                                    <img src=${item.image} alt=""/>
                                </div>
                            </a>
                            <a href="detai.html" class="title">${item.title}</a>
                            <div class="address">${item.address}</div>
                            <div class="area">${item.area}</div>
                            <div class="price">${item.price}</div>
                            <div class="date">${item.date}</div>
                            <a href="detai.html">Chi Tiết</a>
                        </div>`
              );
            });
            that.$element.find('.loader').remove();
            that.$element.append(result);
          })
          .fail(function(e) {
            console.log(e);
          });
    }
  }