@Plugin

export default class Login {

  init () {
    const that = this;
    this.$form = this.$element.find('#login-form');

      this.$form.on('submit', function (e) {
        e.preventDefault();
        that.validate();
        that.loginRequest();
      });
  }
  validate () {
    const that = this;
      const username = that.$form.find('input[name="userName"]');
      const password = that.$form.find('input[name="password"]');
      if (username.val() === "") {
        username.next('.error').removeClass('hidden');
      } else {
        username.next('.error').addClass('hidden');
      }
      if (password.val() === "") {
        password.next('.error').removeClass('hidden');
      } else {
        password.next('.error').addClass('hidden');
      }
  }

  loginRequest() {

    const that = this;
    const userName = that.$form.find("input[name='userName']").val();
    const password = that.$form.find("input[name='password']").val();

    const formData = {
      userName: userName,
      password: password,
    };

    $.ajax({
      method: that.options.method,
      url: that.options.url,
      dataType: 'json',
      data: formData,

      // beforeSend(){
      //   const loader = `<div class="loader"></div>`;
      //   that.$element.append(loader);
      // }
    })
      .done(function(res) {
        if(res.data?.accessToken) {
          const token = res.data.accessToken;
          //login success
          //get user info
          window.localStorage.setItem('token', token);
          window.location.replace("index.html");
        } else {
          console.log(false);
          //login failed
        }
      })
      .fail(function(e) {
        console.log(e);
      });
  }


}