@Plugin({
    options: {}
  })
  export default class Detai { //api là tên data trong thẻ html data-api
    init () {
      const that = this;
      that.fetchApi();
    }
    fetchApi () {
      const that = this;

      $.ajax({
        method: that.options.method,
        url: that.options.url,
        dataType: 'json',
        beforeSend(){
          const loader = `<div class="loader"></div>`;
          that.$element.append(loader);
        }
      })
          .done(function(res) {
            const { data } = res;
            const result = data.map( item => {
              return (
                `<div class="detai">
                    <div class="title" ,style = "text-transform: uppercase;">${item.title}</a>
                    <div class="address"><b>Địa chỉ: </b>${item.address}</a>
                    <div class="location"><b>Địa điểm tìm kiếm: </b>${item.location}</div>
                    <div class="price"><b>Giá: </b>${item.price}</div>
                    <div class="area"><b>Diện tích: </b>${item.area}</div>
                    <div class="img">
                      ${item.images.map(item => (
                        `<img src="${item}" alt="" />`
                      )).join(' ')}
                    </div>
                    <div class="desc"><b>Thông tin chi tiết:</b> ${item.desc}</div>
                </div>`
              );
            });
            that.$element.find('.loader').remove();
            that.$element.append(result);
          })
          .fail(function(e) {
            console.log(e);
          });
    }
  }
